.list-component {
    background: white;
    width: 412px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); /* 그림자 */ 
    margin: 0 auto; /* 페이지 중앙 정렬 */
    margin-top: 4rem;
  }
  
  .title {
    padding: 1rem;
    font-size: 2.5rem;
    text-align: center;
    font-weight: 100;
    background: #22cf29;
    color: white;
  }
  
  .form-wrapper {
    padding: 1rem;
    border-bottom: 1px solid #22cf29;
  }
  
  .todos-wrapper {
    min-height: 5rem;
  }

  .palette-wrapper {
    padding: 1rem;
    background: #f1f3f5;
  }